:root {
  --main-bg: #fae9c8;
  --secondary: #50585e;
  --third: #cc5713;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#fae9c8!important;
  background-color:var(--main-bg)!important;
  font-family: "Lora", sans-serif!important;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/* This allows the footer to stay on the bottom */
.App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.site-container {
    flex: 1 1;
    align-items: flex-start;
    margin-top: 1rem;
}
ul {
    margin: 0;
}
.links {
    padding: 1rem 2rem;
    font-size: 1.25rem;
}
nav ul li:hover {
    color: #fff;
    background-color: var(--third);
    transition: all 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}
nav {
    padding: 0.75rem;
}
.site-title {
    font-size: 2rem;
    font-weight: bold;
}
.nav-links {
    display: block;
    z-index: 100;
}
.active{
    color: #fff;
    background-color: var(--third);
}

li {
    font-size: 1rem;
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin: 0 1.5rem;
}

.hamburger {
    display: none;
}

@media only screen and (max-width: 800px) {
    nav {
        flex-direction: column;
        
    }

    .site-title {
        margin-top: 1rem;
       align-self: flex-start;
       cursor: pointer;
       font-size: 1.5rem;
    }

    .nav-links{
        width: 100%;
        height: 100%;
        top: 5.5rem;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        position: fixed;
        background-color: var(--secondary);
        padding: 1rem 1.5rem;
        z-index: 100;
        color: white;
    }

    .nav-is-open {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }

    .nav-transition {
        transition: -webkit-transform 0.3s ease-in-out;
        transition: transform 0.3s ease-in-out;
        transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    }
    .nav-ul {
        align-items: center;
    }
    li {
        font-size: 2rem;
        margin: 0.5rem;
    }
    .hamburger {
        display: block!important;
        position: absolute;
        top: 2.75rem;
        right: 2rem;
        cursor: pointer;
    }
    .header {
        flex-direction: column;
    }

    ul {
        flex-direction: column;
        justify-content: right;
    }
}

.about-hero {
    font-size: 2rem;
    font-weight: bold;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    
}

.about-pic-para {
    display: flex;
    flex-direction: column;
}

.logos {
    width: 2.5rem!important;
    margin: 0.25rem!important;
}
.logos-container {
    white-space: nowrap;
    margin-top: 1.5rem;
    /* animation: materialize ease-in 0.3s backwards; */
    
}
.logos-container img {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    
}
.logos-container img:nth-child(1) {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}
.logos-container img:nth-child(2) {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}
.logos-container img:nth-child(3) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.logos-container img:nth-child(4) {
    -webkit-animation-delay: 1.1s;
            animation-delay: 1.1s;
}
.logos-container img:nth-child(4) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
.logos-container img:nth-child(5) {
    -webkit-animation-delay: 1.3s;
            animation-delay: 1.3s;
}
.logos-container img:nth-child(6) {
    -webkit-animation-delay: 1.4s;
            animation-delay: 1.4s;
}
.logos-container img:nth-child(7) {
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}
.headshot{
    width: 350px;
    margin-bottom: 2rem;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}
.about-info p {
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.75rem;
    text-align: center;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

@media only screen and (min-width: 1000px) {
    .about-hero {
        font-size: 3rem;
    
    }
    
    .about-pic-para {
        flex-direction: row;
        margin-top: 2rem;

    }

    .about-info {
        align-self: center;
        margin-left: 4rem;
    }
    p {
        line-height: 2.5rem;
        margin-bottom: 2rem;
        font-size: 1.5rem;
    }
}
.skills-title {
    width: 90%;
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid black;
}
.skills-text {
    padding-left: 0.5rem;
    font-size: 1.25rem;
    font-weight: 500;
    text-align: left;
    line-height: 2;
    opacity: 0;
}
.skills-info {
    margin-top: 2rem;
}

.active-skill {
    width: auto;
    height: 3rem;
    margin-bottom: 0;
    margin: 0rem 0.5rem;
    cursor: pointer;
    border-bottom: 4px solid var(--third);
}

.skill-icons {
    width: auto;
    height: 2rem;
    margin-bottom: 0;
    margin: 0rem 0.5rem;
    cursor: pointer;
}

.skills-container {
    display: flex;
    width: 100%;
    padding: 1rem 2rem 1rem 2rem;
    flex-wrap: nowrap;
    overflow-x: auto;
}
.skills-extra-info {
    background-color: #fcfcfc;
    /* this allows for new lines in a text string */
    white-space: pre-wrap;
    width: 95%;
    border-radius: 5px;
    height: 20rem;
    box-shadow: 0 3px 5px rgba(0,0,0, 0.1);
}
.skill-selection {
    font-size: 1.5rem;
    font-weight: 700;
}
@media only screen and (min-width: 800px) {
    .skills-container {
        justify-content: center;
    }
    .skills-extra-info {
        width: 60%!important;
        height: 10rem;
    }
    .skills-text {
        font-size: 1rem!important;
    }
}

@media only screen and (min-width: 1000px) {
    .skills-extra-info {
        width: 40%!important;
        
    }
    .skills-text {
        font-size: 1.2rem!important;
    }
}
/* Animations for page */

.wipe {
    -webkit-animation: wipedown ease-in-out 0.5s backwards;
            animation: wipedown ease-in-out 0.5s backwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-transform-origin: top;
            transform-origin: top;
}
.wipe2 {
    -webkit-animation: wipedown ease-in-out 0.5s backwards;
            animation: wipedown ease-in-out 0.5s backwards;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-transform-origin: top;
            transform-origin: top;
}

.text-appear {
    -webkit-animation: materialize ease-in 0.3s forwards;
            animation: materialize ease-in 0.3s forwards;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;

}
.text-appear-2 {
    -webkit-animation: materialize ease-in 0.3s forwards;
            animation: materialize ease-in 0.3s forwards;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;

}
.skills-container img:hover {
    -webkit-transform:translateY(-7px);
            transform:translateY(-7px);
}
.skills-container img {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;

}
.skills-container img:nth-child(1) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.skills-container img:nth-child(2) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
 
}
.skills-container img:nth-child(3) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
    
}
.skills-container img:nth-child(4) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;

}
.skills-container img:nth-child(5) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
    
}
.skills-container img:nth-child(6) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.skills-container img:nth-child(7) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.7s;
            animation-delay: 0.7s;
    
}
.skills-container img:nth-child(8) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
    
}
.skills-container img:nth-child(9) {
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
    
}

@-webkit-keyframes materialize {
    from {
        opacity: 0;
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
               
      }
      
      to {
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
      }

}

@keyframes materialize {
    from {
        opacity: 0;
        -webkit-transform: translateY(-5px);
                transform: translateY(-5px);
               
      }
      
      to {
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
      }

}

@-webkit-keyframes wipedown {
    from {
        
       -webkit-transform: scaleY(0);
        
               transform: scaleY(0);
        
    }
    to {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
        
    }
}

@keyframes wipedown {
    from {
        
       -webkit-transform: scaleY(0);
        
               transform: scaleY(0);
        
    }
    to {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
        
    }
}


.resume-title {
    font-size: 2rem;
    font-weight: bold;
    margin: 2rem 0rem;
    -webkit-animation: materialize 0.3s ease-in-out;
            animation: materialize 0.3s ease-in-out;
}

.resume-icon {
    width: 1.5rem;
    margin: 0;
}

.resume-link {
    margin-bottom: 2rem;
    width: 10rem;
    cursor: pointer;
    display: none;
    padding: 0.75rem 0.75rem!important;
    border: none!important;
    background-color:var(--secondary)!important;
    -webkit-animation: materialize ease-in 0.6s backwards;
            animation: materialize ease-in 0.6s backwards;
}

.resume-link:hover {
    color: #fff!important;
    background-color: var(--third)!important;
    transition: all 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
    
}
.resume-link:hover > img {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    transition: all 0.6s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.contact-title {   
    font-size: 2rem;
    font-weight: bold;
    margin-top: 1rem;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
}
.contact-form {
    margin-top: 2rem;
    padding: 1rem!important;
    background-color: var(--secondary);
    border-radius: 5px;
    width: 90%;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
    color: white;
}

@media only screen and (min-width: 700px) {
    .contact-form {
        width: 450px
    }
}
.projects-title {
    font-size: 2rem;
    font-weight: bold;
    -webkit-animation: materialize ease-in 0.3s backwards;
            animation: materialize ease-in 0.3s backwards;
    margin: 2rem 0rem;
}

.project-card {
    transition: background-color 0.3s ease-in, -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, background-color 0.3s ease-in;
    transition: transform 0.3s ease-in-out, background-color 0.3s ease-in, -webkit-transform 0.3s ease-in-out;
    position: relative;
    background-color: var(--secondary)!important;
    cursor: pointer;
    padding: 0!important;
    margin-bottom: 1rem;
    grid-column: 1 / span 12;
    color: whitesmoke;
    overflow: hidden;
    height: 16rem;
}

.project-card:hover {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    z-index: 100;
    color: white;
    background-color: #000!important;
}

.project-card:hover .link-to-site {
    visibility: visible;
    
}
.project-card .card-img-top {
    height: 12rem;
}
.project-card:hover .card-img-top {
    opacity: 0.3;
}

.project-card:hover .card-body {
    top: -100px;
    background-color: #000;
}
.card-body {
    height: 8rem;
    padding: 0.75rem 1rem!important;
    transition: top 0.3s ease-in-out, background-color 0.3s ease-in-out;
    position: relative;
    background-color: var(--secondary);
    top: 0;
}

.card-body p {
    line-height: 1.5;
}
.link-to-site {
    position: absolute;
    top: 15px;
    left: 50%;
    margin-left: -95px;
    margin-top: -100px;
    z-index: 1000;
    visibility: hidden;
    
}

.link-to-site .link-images {
    height: 4rem;
    width: 4rem;
    color: rgba(255, 255, 255, 1.0)!important;
    z-index: 1000;
    margin: 0.5rem; 
}
.link-to-site a {
    text-decoration: none!important;
    color: white;
}
.link-to-site a:hover {
    text-decoration: none!important;
    color: white;
}
.link-images-no-live {
    margin-left: 55px!important;
}
.link-to-site .link-images:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.single-project-title {
    font-weight: bold;
    font-size: 1.5rem;
}

.projects-container div:nth-child(1){
    -webkit-animation: materialize ease-in 0.6s backwards;
            animation: materialize ease-in 0.6s backwards;

}
.projects-container div:nth-child(2){
    -webkit-animation: materialize ease-in 0.8s backwards;
            animation: materialize ease-in 0.8s backwards;

}
.projects-container div:nth-child(3){
    -webkit-animation: materialize ease-in 1s backwards;
            animation: materialize ease-in 1s backwards;

}
.projects-container div:nth-child(4){
    -webkit-animation: materialize ease-in 1.2s backwards;
            animation: materialize ease-in 1.2s backwards;

}
.projects-container div:nth-child(5){
    -webkit-animation: materialize ease-in 1.4s backwards;
            animation: materialize ease-in 1.4s backwards;

}
.projects-container div:nth-child(6){
    -webkit-animation: materialize ease-in 1.6s backwards;
            animation: materialize ease-in 1.6s backwards;

}

.projects-container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding-top: 2rem;
    
}

/* GRID FOR PROJECT CARDS */
@media only screen and (min-width: 800px) {
    .project-card {
        height: 15rem;
        margin-left: 0.25rem;
        margin-right: 0.25rem;
    }
    .adjusted-for-no-live {
        top: -15px;
    }
    .card-description {
        font-size: 1rem;
    }
    .link-to-site {
        top: 25px;
    }
    .project-card:nth-child(even) {
        grid-column: 7 / span 6;
    }
    .project-card:nth-child(odd) {
        grid-column: 1 / span 6;
    }

}
@media only screen and (min-width: 1000px) {
    .project-card {
        height: 16rem;
    }
    .link-to-site {
        top: 15px;
    }
    .adjusted-for-no-live {
        top: 0px;
    }
}
@media only screen and (min-width: 1200px) {
    
    .project-card img {
        height: 15rem;
    }
    
    .project-card:nth-child(3n){
        grid-column: 9 / span 4;
    }
    .project-card:nth-child(3n-2){
        grid-column: 1 / span 4;
    }
    .project-card:nth-child(3n-1){
        grid-column: 5 / span 4;
    }

}
.footer {
    background-color: var(--third);
    margin-top: 2rem;
}
.footer img {
    margin: 1rem 0.5rem 1rem 0;
    width: 1.15rem;
    cursor: pointer;
}

.footer p {
    font-size: 0.85rem;
    margin: 0 0.5rem 0 0;
    
}

@media only screen and (max-width: 800px) {
    .footer img {
        width: 1.8rem;
        
    }
}


